import React from 'react';
import styles from './Article2.module.scss';
import Array2 from '../Arrays/Array2';
import '../aos.css';
import AOS from 'aos';

class Article2View extends React.Component {
    componentDidMount(){
        AOS.init({duration : 1000});
    }
    render(){
        return (
            <div className={styles.A1wrapper}>
            <h1>Przemoc domowa w czasie kwarantanny</h1>
            {
                Array2.map(item => (
                    <>
                        <div className={styles.wrapper}>
                            <div className={styles.contentDiv}>
                                <p data-aos="fade-left">{item.description1}</p>
                                <img data-aos="fade-right" src={item.image1} />
                            </div>
                            <div className={styles.contentDiv}>
                                <p data-aos="fade-left">{item.description2}</p>
                                <img data-aos="fade-right" src={item.image2} />
                            </div>
                        </div>
                    </>
                ))
            }
        </div>
        )
    }
}
export default Article2View;