import React from 'react';
import styles from './Navbar.module.scss';
import { NavLink } from 'react-router-dom';

const Navbar = () => (
    <>
            <ul className={styles.ul}>
            <h1 className={styles.h1}>Wybierz Artykuł</h1>

                <li className={styles.listItem2}></li>
                <li className={styles.listItem}><NavLink exact activeClassName={styles.choosed} className={styles.link} to="/">Artykuł 1</NavLink></li>
                <li className={styles.listItem}><NavLink exact activeClassName={styles.choosed} className={styles.link} to="/article1">Artykuł 2</NavLink></li>
                <li className={styles.listItem}><NavLink exact activeClassName={styles.choosed} className={styles.link} to="/article2">Artykuł 3</NavLink></li>
                <li className={styles.listItem}><NavLink exact activeClassName={styles.choosed} className={styles.link} to="/article3">Artykuł 4</NavLink></li>
            </ul>
    </>
)
export default Navbar;