import React from 'react';
import styles from './Article4.module.scss';
import '../aos.css';
import AOS from 'aos';
import Array4 from '../Arrays/Array4';

class Article4View extends React.Component {
    componentDidMount(){
        AOS.init({duration : 1000});
    }
    render(){
        return (
            <div className={styles.A1wrapper}>
            <h1>Depresja</h1>
            {
                Array4.map(item => (
                    <>
                        <div className={styles.wrapper}>
                            <div className={styles.contentDiv}>
                                <p data-aos="fade-left">{item.description1}</p>
                                <img data-aos="fade-right" src={item.image1} />
                            </div>
                            <div className={styles.contentDiv}>
                                <p data-aos="fade-left">{item.description2}</p>
                                <img data-aos="fade-right" src={item.image2} />
                            </div>
                        </div>
                    </>
                ))
            }
        </div>
        )
    }
}

export default Article4View;