import React from 'react';
import Navbar from './components/Navbar/Navbar';
import Article1View from './components/Article1/Article1View';
import Article2View from './components/Article2/Article2View';
import Article3View from './components/Article3/Article3View';
import Article4View from './components/Article4/Article4View';
import {BrowserRouter,Route,Switch} from 'react-router-dom';

class App extends React.Component {
  render(){
    return (
        <BrowserRouter>
          
            <Navbar />
            <Switch>
              <Route exact path="/" component={Article1View} />
              <Route exact path="/article1" component={Article2View} />
              <Route exact path="/article2" component={Article3View} />
              <Route exact path="/article3" component={Article4View} />
            </Switch>
        </BrowserRouter>
    )
  }
}
export default App;